import {ProfileAPI, UsersAPI} from "../API/api";

const AddPostCreator = "ADD-NEW-POST";
const setUserProfileCreator = "SET-USER-PROFILE";
const SetStatusCreator = "SET-STATUS";
const answerCommentCreator = "ANSWER-COMMENT";
const setFollowCreator = "SET-FOLLOW";
const ToggleIsFollowingProgressCreator = "TOGGLE-IS-FOLLOWING-PROGRESS";
const SetCurrentUserIdCreator = "SET-CURRENT-USER-ID";
const setLikeCreator = "SET-LIKE-CREATOR";
const setLikeAnswerCreator = "SET-LIKE-ANSWER-CREATOR";
const acceptCommentChangesCreator = "ACCEPT-COMMENT-CHANGES";
const deleteCommentCreator = "DELETE-COMMENT";
const acceptAnswerChangesCreator = "ACCEPT-ANSWER-CHANGES";
const deleteAnswerCreator = "DELETE-ANSWER";

let InitialState = {
    profile: {
        photos: {
            small: null, large: null
        }
    },
    currentUserId: null,
    isFetching: true,
    PostItem: [],
    status: null,
    isFollowing: null,
    followingInProgress: null
};


function ProfileReducer(state = InitialState, action) {
    switch (action.type) {
        case AddPostCreator:
            return {
                ...state, PostItem: [...state.PostItem, {
                    id: Object.keys(state.PostItem).length + 1,
                    userId: action.userId,
                    fullName: action.fullName,
                    usersImage: action.currentProfileImage,
                    commentMessage: action.NewPostMessage,
                    likesCount: action.likesCount,
                    isLiked: action.isLiked,
                    answers: []
                }]
            };
        case SetStatusCreator:
            return {...state, status: action.status};
        case SetCurrentUserIdCreator:
            return {...state, currentUserId: action.userId}
        case setUserProfileCreator:
            return {...state, profile: action.user};
        case answerCommentCreator:
            return {
                ...state, PostItem: [...state.PostItem.map(post => {
                    if (post.id === action.id) {
                        return {
                            ...post, answers: [...post.answers, {
                                id: Object.keys(post.answers).length + 1,
                                postId: action.id,
                                answerName: action.name,
                                usersImage: action.image,
                                userId: action.userId,
                                answerMessage: action.message,
                                isLiked: action.isLiked,
                                likesCount: action.likesCount
                            }]
                        }
                    }
                    return post;
                })]
            }
        case setLikeAnswerCreator:
            return {
                ...state, PostItem: [...state.PostItem.map(post => {
                    return {
                        ...post, answers: [...post.answers.map(answer => {
                            if (answer.id === action.answerId && post.id === action.postId) return {
                                ...answer,
                                likesCount: !answer.isLiked ? answer.likesCount + 1 : answer.likesCount - 1,
                                isLiked: !answer.isLiked ? true : false
                            }
                            return answer;
                        })]
                    }
                })]
            }
        case setFollowCreator:
            return {...state, isFollowing: action.followingData};
        case ToggleIsFollowingProgressCreator:
            return {...state, followingInProgress: action.isFetching};
        case setLikeCreator:
            return {
                ...state, PostItem: [...state.PostItem.map(post => {
                    if (post.id === action.postId) return {
                        ...post,
                        likesCount: !post.isLiked ? post.likesCount + 1 : post.likesCount - 1,
                        isLiked: post.isLiked ? false : true
                    }
                    return post;
                })]
            }
        case acceptCommentChangesCreator:
            return {
                ...state, PostItem: [...state.PostItem.map(post => {
                    return {
                        ...post, commentMessage: post.id === action.id ? action.newMessage : post.commentMessage
                    }
                })]
            }
        case deleteCommentCreator:
            return {
                ...state, PostItem: [...state.PostItem.filter(post => post.id !== action.postId)]
            }
        case acceptAnswerChangesCreator:
            return {
                ...state, PostItem: [...state.PostItem.map(post => {
                    return {
                        ...post, answers: [...post.answers.map(answer => {
                            return {
                                ...answer, answerMessage: answer.id === action.answerId && post.id === action.postId
                                    ? action.newMessage : answer.answerMessage
                            }
                        })]
                    }
                })]
            }
        case deleteAnswerCreator:
            return {
                ...state, PostItem: [...state.PostItem.map(post => {
                    return {...post, answers: [...post.answers.filter(answer => answer.id !== action.answerId)]}
                })]
            }
        default:
            return state;
    }

}

export const addPost = (userId, fullName, currentProfileImage, NewPostMessage, likesCount, isLiked) => ({
    type: AddPostCreator, userId, fullName, currentProfileImage, NewPostMessage, likesCount, isLiked
});
export const setUserProfile = user => ({type: setUserProfileCreator, user});
export const AnswerComment = (id, userId, name, image, message, likesCount, isLiked) => ({
    type: answerCommentCreator, id, userId, name, image, message, likesCount, isLiked
});
export const setFollowingData = followingData => ({type: setFollowCreator, followingData});
export const ToggleIsFollowingProgress = isFetching => ({
    type: ToggleIsFollowingProgressCreator,
    isFetching
});
export const SetStatus = status => ({type: SetStatusCreator, status});
export const setLike = postId => ({type: setLikeCreator, postId});
export const setLikeAnswer = (answerId, postId) => ({type: setLikeAnswerCreator, answerId, postId});
export const acceptCommentChanges = (id, newMessage) => ({type: acceptCommentChangesCreator, id, newMessage});
export const deleteComment = postId => ({type: deleteCommentCreator, postId});
export const acceptAnswerChanges = (answerId, newMessage, postId) => ({
    type: acceptAnswerChangesCreator, answerId, newMessage, postId
});
export const deleteAnswer = answerId => ({type: deleteAnswerCreator, answerId});
export const getProfile = userId => async dispatch => {
    let profileData = await ProfileAPI.getUserProfile(userId);
    let statusData = await ProfileAPI.getProfileStatus(userId);
    dispatch(setUserProfile(profileData));
    dispatch(SetStatus(statusData));
}

export const setStatusProfile = status => async dispatch => {
    let data = await ProfileAPI.setProfileStatus(status)
    if (data.resultCode === 0) dispatch(SetStatus(status));
}

export const getFollowingData = userId => async dispatch => {
    let data = await ProfileAPI.getFollowingData(userId)
    if (data.resultCode === 0) dispatch(setFollowingData(data));
}

export const Follow = id => async dispatch => {
    dispatch(ToggleIsFollowingProgress(true));
    let data = await UsersAPI.follow(id);
    if (data.resultCode === 0) dispatch(setFollowingData(true));
    dispatch(ToggleIsFollowingProgress(false));
}

export const unFollow = id => async dispatch => {
    dispatch(ToggleIsFollowingProgress(true));
    let data = await UsersAPI.unfollow(id);
    if (data.resultCode === 0) dispatch(setFollowingData(false));
    dispatch(ToggleIsFollowingProgress(false));
}


export default ProfileReducer;