const SearchingCommunitiesCreator = "SEARCHING-COMMUNITIES-CREATOR"
const SetCommunitiesCreator = "SET-COMMUNITIES-CREATOR"
const FollowCreator = "FOLLOWING-IN-COMMUNITY"
const unFollowCreator = "UNFOLLOWING-IN-COMMUNITY"

let InitialState = {
    Communities: [],
};


function CommunityReducer(state = InitialState, action) {
    switch (action.type) {
        case SearchingCommunitiesCreator:
            return state;
        case SetCommunitiesCreator:
            return {...state, Communities: action.CommunityItem}
        case FollowCreator:
            return {
                ...state, Communities: [
                    ...state.Communities.map(i => {
                        if (i.CommunityId === action.CommunityId) {
                            return {...i, isFollowed: true}
                        } else {
                            return {...i}
                        }
                    })
                ]
            }
        case unFollowCreator:
            return {
                ...state, Communities: [
                    ...state.Communities.map(i => {
                        if (i.CommunityId === action.CommunityId) {
                            return {...i, isFollowed: false}
                        } else {
                            return {...i}
                        }
                    })
                ]
            }
        default:
            return state;
    }
}

export const SearchingCommunities = () => ({type: SearchingCommunitiesCreator});
export const SetCommunities = (CommunityItem) => ({type: SetCommunitiesCreator, CommunityItem});
export const Follow = (CommunityId) => ({type: FollowCreator, CommunityId});
export const unFollow = (CommunityId) => ({type: unFollowCreator, CommunityId});
export default CommunityReducer;