import React, {Suspense, useEffect} from "react";
import './App.css';
import {BrowserRouter, Navigate, Route, Routes} from "react-router-dom";
import Header from "./components/Header/Header";
import Navbar from "./components/Navbar/Navbar";
import {initiliazeApp} from "./redux/AppReducer";
import {Provider, useDispatch, useSelector} from "react-redux";
import InitializationPage from "./components/Preloader/Initialization/InitializationPage";
import store from "./redux/ReduxStore";


const Auth = React.lazy(() => import("./components/Auth/Auth.jsx"));
const Login = React.lazy(() => import("./components/Auth/Login/Login"));
const Register = React.lazy(() => import("./components/Auth/Register/Register"));
const Dialogs = React.lazy(() => import("./components/Dialogs/Dialogs"));
const Profile = React.lazy(() => import("./components/Profile/Profile"));
const Friends = React.lazy(() => import("./components/Friends/Friends"));
const Feed = React.lazy(() => import("./components/Feed/Feed"));
const Community = React.lazy(() => import("./components/Community/Community"));
const Video = React.lazy(() => import("./components/Video/Video"));
const Music = React.lazy(() => import("./components/Music/Music"));
const Settings = React.lazy(() => import("./components/Settings/Settings"));


function App() {
    const [initialized, isAuth, userId] = useSelector(state => [state.App.initialized, state.AuthPage.isAuth, state.AuthPage.userId]);
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(initiliazeApp());
    }, []);

    if (!initialized) {
        return <InitializationPage/>
    } else if (!isAuth) {
        return <Suspense fallback={<InitializationPage/>}>
            <BrowserRouter>
                <div className="AuthContainer">
                    <Routes>
                        <Route path='*' element={<Auth/>}/>
                        <Route path='/login' element={<Login/>}/>
                        <Route path='/register' element={<Register/>}/>
                    </Routes>
                </div>
            </BrowserRouter>
        </Suspense>
    }

    return <div className='app-wrapper'>
        <Header/>
        <Navbar/>
        <div className="app-wrapper-content">
            <Routes>
                <Route path='/user/:userId' element={<Profile/>}/>
                <Route path='/dialogs' element={<Dialogs/>}/>
                <Route path='/dialogs/:dialogId' element={<Dialogs/>}/>
                <Route path='/feed' element={<Feed/>}/>
                <Route path='/community' element={<Community/>}/>
                <Route path="/friends" element={<Friends/>}/>
                <Route path='/video' element={<Video/>}/>
                <Route path='/music' element={<Music/>}/>
                <Route path='/settings' element={<Settings/>}/>
                <Route path='*' element={<Navigate to="/feed"/>}/>
            </Routes>
        </div>
    </div>
}

function AppProvider() {
    return <Suspense fallback={<InitializationPage/>}>
        <BrowserRouter>
            <Provider store={store}>
                <App/>
            </Provider>
        </BrowserRouter>
    </Suspense>

}

export default AppProvider;
