import React from 'react';
import '../../App.css'
import styles from "./Nav.module.css"
import {NavLink} from 'react-router-dom'
import user from "../../assets/Profile/usersProfileIcon.png"
import feed from "../../assets/Navbar/feed.png"
import messenger from "../../assets/Navbar/messenger.png"
import music from "../../assets/Navbar/music.png"
import video from "../../assets/Navbar/video.png"
import settings from "../../assets/Navbar/setting.png"
import friends from "../../assets/Navbar/friends.png"
import community from "../../assets/Navbar/community.png"
import {useSelector} from "react-redux";
import loader from "../../assets/loader.svg"
import Preloader from "../Preloader/Preloader";

function Navbar() {
    const [usersPhoto, fullName, userId] = useSelector(state => [
        state.AuthPage.currentProfileImage.small,
        state.AuthPage.login,
        state.AuthPage.userId
    ]);

    return <nav className={styles.Nav}>
        <NavLink to={"/user/" + userId}>
            <button className={styles.ProfileBlock}>
                <span><img src={usersPhoto != null ? usersPhoto : user}/></span>
                <span className={styles.profileBlock}>{fullName != null ? fullName : <Preloader/>}</span>
            </button>
        </NavLink>
        <NavLink to="/feed">
            <button className={styles.NavButton}><img src={feed}/>
                News
            </button>
        </NavLink>
        <NavLink to="/dialogs">
            <button className={styles.NavButton} type='submit'><img
                src={messenger}/>
                Messenger
            </button>
        </NavLink>
        <NavLink to="/community">
            <button className={styles.NavButton} type='submit'><img src={community}/>
                Community
            </button>
        </NavLink>
        <NavLink to="/music">
            <button className={styles.NavButton}><img className="Music"
                         src={music}/>
                Music
            </button>
        </NavLink>
        <NavLink to="/videos">
            <button className={styles.NavButton}><img
                src={video}/>
                Video
            </button>
        </NavLink>
        <NavLink to={"/Friends"}>
            <button className={styles.NavButton}>
                <img src={friends}/>
                Friends
            </button>
        </NavLink>
        <NavLink to="/settings">
            <button className={styles.NavButton}><img
                src={settings}/>
                Settings
            </button>
        </NavLink>
    </nav>
};

export default Navbar;