import {getCurrentLogo, getUserData} from "./AuthReducer";

const SetInitiliazedCreator = "SET-INITIALIZED"

let InitialState = {
    initialized: false
};

function AppReducer(state = InitialState, action) {
    switch (action.type) {
        case SetInitiliazedCreator:
            return {
                ...state, initialized: true
            }
        default:
            return state;
    }
}

export const SetInitiliazing = () => ({type: SetInitiliazedCreator});

export const initiliazeApp = () => async dispatch => {
    let promises = [];
    promises.push(dispatch(getUserData()));
    await Promise.all(promises);
    dispatch(SetInitiliazing());
}

export default AppReducer;