import {AuthAPI} from "../API/api";
import {stopSubmit} from "redux-form";

const setUserDataCreator = "SET-USER-DATA";
const SetCurrentUsersPhotoCreator = "SET-CURRENT-USERS-PHOTO";
const LoginCreator = "LOGIN-CREATOR";
const RegisterCreator = "REGISTER-CREATOR";
const setUsersPhotoCreator = "SET-USERS-PHOTO-CREATOR";


let InitialState = {
    userId: null,
    login: null,
    email: null,
    isAuth: false,
    currentProfileImage: {
        small: null,
        large: null
    },
    isFetching: false
};

function AuthReducer(state = InitialState, action) {
    switch (action.type) {
        case setUserDataCreator:
            return {...state, ...action.data, isAuth: true};
        case LoginCreator:
            return {...state, isAuth: action.bool}
        case RegisterCreator:
            return state;
        case SetCurrentUsersPhotoCreator:
            return {
                ...state, currentProfileImage: {
                    ...state.currentProfileImage,
                    small: action.small,
                    large: action.large
                }
            }
        default:
            return state;
    }
}

export const setUserData = (userId, login, email) => ({type: setUserDataCreator, data: {userId, login, email}});
export const setLoginStatus = (bool) => ({type: LoginCreator, bool});
export const setCurrentPhoto = (large, small) => ({type: SetCurrentUsersPhotoCreator, large, small});
export const RegisterConfirm = (email, login, password, repeatPassword) => ({
    type: RegisterCreator, email, login, password, repeatPassword
});
export const setUsersPhoto = (small, large) => ({type: setUsersPhotoCreator, small, large});

export const getUserData = () => async dispatch => {
    let data = await AuthAPI.getUsersData()
    if (data.resultCode === 0) {
        let {id, login, email} = data.data
        dispatch(setUserData(id, login, email))
    }
}

export const getCurrentLogo = userId => async dispatch => {
    let photos = await AuthAPI.GetProfileImage(userId)
    dispatch(setCurrentPhoto(photos.large, photos.small));
}

export const LoginSystem = (email, password, rememberMe) => async dispatch => {
    let data = await AuthAPI.Login(email, password, rememberMe)
    if (data.resultCode === 0) {
        dispatch(setLoginStatus(true));
        dispatch(setUserData());
    } else {
        let message = data.messages.length > 0 ? data.messages[0] : "Some Error"
        dispatch(stopSubmit("login", {_error: message}));
    }
}

export const LogOutSystem = () => async dispatch => {
    let data = await AuthAPI.LogOut()
    if (data.resultCode === 0) {
        dispatch(setLoginStatus(false));
        dispatch(setUserData(null, null, null));
    }
};

export const setNewCurrentUsersPhoto = photo => async dispatch => {
    let Response = await AuthAPI.setCurrentPhoto(photo);
    if (Response.data.resultCode === 0) dispatch(setUsersPhoto(Response.data.small, Response.data.large));
}
export default AuthReducer;