const SendMsgCreator = "SEND-NEW-MESSAGE";

let InitialState = {
    Dialogs: [
        {
            id: 1,
            userId: 1,
            name: "Winston Smith",
            logo: "https://i.ytimg.com/vi/2AM9KnjWy5E/maxresdefault.jpg",
            Messages: [
                {
                    messageId: 1,
                    userId: 1,
                    message: "peace",
                    date: {
                        hours: 10,
                        minutes: 10
                    }
                },
                {
                    messageId: 2,
                    userId: 2,
                    message: "is war",
                    date: {
                        hours: 12,
                        minutes: 12
                    }
                },
            ],
        },
        {
            id: 2,
            userId: 2,
            name: "Julia Dixon",
            logo: "https://a1cf74336522e87f135f-2f21ace9a6cf0052456644b80fa06d4f.ssl.cf2.rackcdn.com/images/characters/large/800/Julia.1984.webp",
            LastMessage: "I love you",
            Messages: [],
            date: "23:15"
        },
        {
            id: 3,
            userId: 3,
            name: "Emmanuel Goldstein",
            logo: "https://avatars.dzeninfra.ru/get-zen_doc/1875939/pub_604d21ed011181447b2644e6_604d47a4af41a36641ef1288/scale_1200",
            LastMessage: "The revolution will come",
            Messages: [],
            date: "01:15"
        }
    ],
    Messages: [],
}


const DialogsReducer = (state = InitialState, action) => {
    switch (action.type) {
        case SendMsgCreator:
            return {
                ...state, Messages: [...state.Messages, {
                    messageId: Object.keys(state.Messages).length + 1,
                    message: action.MessageText,
                    userId: action.userId,
                    fullName: action.fullName,
                    usersPhoto: action.usersPhoto,
                }],
            }
        default:
            return state;
    }

}


export const SendMessage = (userId, fullName, usersPhoto, MessageText) => ({type: SendMsgCreator, userId, fullName, usersPhoto, MessageText})

export default DialogsReducer;