import axios from "axios";

const instance = axios.create({
    withCredentials: true, baseURL: "https://social-network.samuraijs.com/api/1.0/",
});

export const UsersAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`).then(Repsonse => {
            return Repsonse.data;
        });
    }, unfollow(id) {
        return instance.delete(`follow/${id}`).then(Response => {
            return Response.data
        });
    }, follow: (id) => {
        return instance.post(`follow/${id}`, {}).then(Response => {
            return Response.data
        });
    }
};

export const ProfileAPI = {
    getUserProfile(userId) {
        return instance.get(`profile/${userId}`).then(Response => {
            return Response.data;
        });
    }, getProfileStatus(userId) {
        return instance.get(`profile/status/${userId}`).then(Response => {
            return Response.data;
        });
    }, setProfileStatus(status) {
        return instance.put("profile/status", {status});
    }, getFollowingData(userId) {
        return instance.get(`follow/${userId}`).then(Response => {
            return Response.data;
        });
    }
};

export const AuthAPI = {
    getUsersData() {
        return instance.get("auth/me").then(Response => {
            return Response.data;
        });
    }, GetProfileImage(userId) {
        return instance.get(`profile/${userId}`).then(Response => {
            return Response.data.photos;
        });
    }, Login(email, password, rememberMe) {
        return instance.post(`auth/login?email=${email}&password=${password}&rememberMe=${rememberMe}`, {}).then(Response => {
            return Response.data;
        });
    }, LogOut() {
        return instance.delete("auth/login").then(Response => {
            return Response.data;
        })
    }, setCurrentPhoto(photo) {
        return instance.put("profile/photo", {photo});
    }
};
