import {UsersAPI} from "../API/api";

const FollowCreator = "FOLLOW";
const unFollowCreator = "UNFOLLOW";
const setUserCreator = "SET-USER"
const SearchFriendCreator = "SEARCH-FRIEND"
const setCurrentPageCreator = "SET-CURRENT-PAGE";
const setUsersTotalCountCreator = "SET-TOTAL-USERS-COUNT"
const ToggleIsFetchingCreator = "TOGGLE-IS-FETCHING"
const ToggleIsFetchingFollowingProgressCreator = "TOGGLE-IS-FETCHING-FOLLOWING-PROGRESS"


let InitialState = {
    users: [],
    SearchMessageText: "",
    pageSize: 50,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
}

const FriendsReducer = (state = InitialState, action) => {
    switch (action.type) {
        case FollowCreator:
            return {
                ...state,
                users: [...state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    } else {
                        return {...u}
                    }
                })],

            }
        case unFollowCreator:
            return {
                ...state,
                users: [...state.users.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: false}
                    } else {
                        return {...u}
                    }
                })],
            }
        case setUserCreator:
            return {
                ...state, users: action.users
            }
        case SearchFriendCreator:
            return 0;
        case setCurrentPageCreator:
            return {...state, currentPage: action.currentPage}
        case setUsersTotalCountCreator:
            return {...state, totalUsersCount: action.totalUsersCount}
        case ToggleIsFetchingCreator:
            return {...state, isFetching: action.isFetching}
        case ToggleIsFetchingFollowingProgressCreator:
            return {
                ...state, followingInProgress: action.isFetching ?
                    [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        default:
            return state;
    }
}

export const acceptFollow = (userId) => ({type: FollowCreator, userId})
export const acceptUnFollow = (userId) => ({type: unFollowCreator, userId})
export const SetUsers = (users) => ({type: setUserCreator, users})
export const SearchFriend = () => ({type: SearchFriendCreator})
export const setCurrentPage = (currentPage) => ({type: setCurrentPageCreator, currentPage})
export const setUsersTotalCount = (totalUsersCount) => ({type: setUsersTotalCountCreator, totalUsersCount})
export const ToggleIsFetching = (isFetching) => ({type: ToggleIsFetchingCreator, isFetching})
export const ToggleIsFollowingProgress = (isFetching, userId) => ({
    type: ToggleIsFetchingFollowingProgressCreator,
    isFetching,
    userId
})

export const getUsers = (currentPage, pageSize) => async dispatch => {
    dispatch(ToggleIsFetching(true))
    let data = await UsersAPI.getUsers(currentPage, pageSize)
    dispatch(ToggleIsFetching(false))
    dispatch(SetUsers(data.items))
    dispatch(setUsersTotalCount(data.totalCount))
}

export const Follow = id => async dispatch => {
    dispatch(ToggleIsFollowingProgress(true, id))
    let data = await UsersAPI.follow(id)
    if (data.resultCode === 0) dispatch(acceptFollow(id))
    dispatch(ToggleIsFollowingProgress(false, id))
}

export const unFollow = id => async dispatch => {
    dispatch(ToggleIsFollowingProgress(true, id))
    let data = await UsersAPI.unfollow(id)
    if (data.resultCode === 0) dispatch(acceptUnFollow(id));
    dispatch(ToggleIsFollowingProgress(false, id));
}


export default FriendsReducer;