const FeedCreatePostCreator = "CREATE-FEED-POST";

let InitialState = {
    FeedPosts: [
        {
            fullName: "Winston Smith",
            usersPhoto: null,
            userId: 2,
            postId: 1,
            postMessage: "Emmanuel, give me a book pls",
        }
    ],
}

const FeedReducer = (state = InitialState, action) => {
    switch (action.type) {
        case FeedCreatePostCreator:
            return {
                ...state,
                FeedPosts: [...state.FeedPosts, {
                    fullName: action.fullName,
                    usersPhoto: action.usersPhoto,
                    userId: action.userId,
                    postId: Object.keys(state.FeedPosts).length + 1,
                    postMessage: action.postText,
                }],
            }
        default:
            return state;
    }
}

export const AddingNewPostFeed = (fullName, usersPhoto, postText, userId) => ({
    type: FeedCreatePostCreator,
    fullName, usersPhoto, postText, userId
});


export default FeedReducer;
